export const createStripeSession =
  (id, paymentResult) => async (dispatch, getState) => {
    try {
      dispatch({
        type: STRIPE_ORDER_PAY_REQUEST,
      });

      const {
        userLogin: { userInfo },
      } = getState();

      const config = {
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };
      const { data } = await axios.put(
        `/api/orders/${id}/create-payment-intent/`,
        paymentResult,
        config,
      );

      dispatch({
        type: STRIPE_ORDER_PAY_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: STRIPE_ORDER_PAY_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };

